  filter: brightness(1.5);
  outline: none;
}

.btn-up:before, .btn-up:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-up:before {
  width: 5px;
  height: 25px;
  background-color: #f5f0f6;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.btn-up:after {
  width: 15px;
  height: 15px;
  border-top: 5px solid #f5f0f6;
